import MasterPage from '../components/MasterPage';
import fetch from 'isomorphic-unfetch';
import { Row, Col } from 'reactstrap';
import BitcoinPrice from '../components/BitcoinPrice';
import BitcoinNews from '../components/BitcoinNews';
import BitcoinEvents from '../components/BitcoinEvents';
//import 'bootstrap/dist/css/bootstrap.min.css';

const Index = (props) => {
    return(
        <MasterPage>
            <div>
                <Row>
                    <Col md="12">
                        <h2>Precio del Bitcoin</h2>
                        <BitcoinPrice price={props.price}/>
                    </Col>
                    {/*<Col md="8">
                        <h2>Noticias sobre Bitcoin</h2>
                        <BitcoinNews news={props.news}/>
    </Col>*/}
                    <Col md="4">
                        <h2>Proximos eventos sobre Bitcoin</h2>
                        <BitcoinEvents events={props.events} />
                    </Col>
                </Row>
            </div>
        </MasterPage>
    )
}

Index.getInitialProps = async () => {

    const criptomoneda = 'BTC';
    const moneda = 'USD';

    const precio = await fetch(`https://min-api.cryptocompare.com/data/pricemultifull?fsyms=${criptomoneda}&tsyms=${moneda}`);

    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&from=2019-07-16&sortBy=publishedAt&apiKey=599b31bd0d614939b8b56da004c5c638&language=es')

    const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Mexico&token=KMFG4XO47WWJGSWOYYC7')

    const priceResp = await precio.json();
    const newsResp = await noticias.json();
    const eventsResp = await eventos.json();

    return {
        price: priceResp.DISPLAY[criptomoneda][moneda],
        news: newsResp.articles,
        events: eventsResp.events
    }
}

export default Index;